import React, { Component } from 'react';
import { BrowserRouter, Route, Switch, NavLink } from 'react-router-dom';
import Gun from './containers/Gun';
import Gun2 from './containers/Gun2';

class App extends Component {
  render() {
    return (

        <BrowserRouter>
          <React.Fragment>

            <NavLink to="/1" className="switchGun">Receiver 1</NavLink>
            <NavLink to="/2" className="switchGun">Receiver 2</NavLink>

          <Switch>
            <Route exact path="/1" component={ Gun } />
            <Route exact path="/2" component={ Gun2 } />
          </Switch>
          </React.Fragment>
        </BrowserRouter>

    )
  }
}

export default App;
