import React from 'react';
import Konva from 'konva';

import { products , categories, mounting_points} from "../products";
import { createCanvas } from '../canvas';


class Gun extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedProduct: {},
            products: [],
            filteredProducts: [],
            categories: [],
            selectedProductCategory: 'Grip'
        };
        this.canvasStage = React.createRef();
    }

    componentDidMount() {
        this.customProduct = categories.Receiver.children[0];
        const categoriesArray = [];
        const windowWidth = window.innerWidth;
        for (let categoryName in categories) {
            if (categoryName !== 'Receiver') {
                categoriesArray.push(categoryName)
            }

        }
        this.setState({
            products : products,
            categories: categoriesArray,
        });

        this._stage = createCanvas(this.canvasStage.current, windowWidth);

        this._layer = new Konva['Layer']();
        this._stage.draw();
        this.renderProduct();

    }

    onSelectProduct = (categoryName, productId) => {
        let selectedElement = categories[categoryName]['children'].find(product => product.id == productId);


        if (categoryName === 'Receiver') {
            this.customProduct = selectedElement;

        } else {
            let connectedSelectedElementWithMountingPoint = mounting_points.filter((element) => {
                return element.category.name === categoryName

            });

            const indexParentReceiver = connectedSelectedElementWithMountingPoint.findIndex((elem) => {
                return elem.parent.id === this.customProduct.id ? true : false
            });


            if (indexParentReceiver === -1) {
                return

            } else {
                connectedSelectedElementWithMountingPoint = connectedSelectedElementWithMountingPoint[indexParentReceiver];
                connectedSelectedElementWithMountingPoint['elem'] = selectedElement;
            }

        }

        this.renderProduct();

    };


    renderProduct = ()  => {
        let imageDetails = this.customProduct['imageDetails'];
        let baseProportions = [
            imageDetails['width'],
            imageDetails['height']
        ];

        if (imageDetails['scale']) {
            baseProportions[0] *= imageDetails['scale'];
            baseProportions[1] *= imageDetails['scale'];
        }

        this._layer['destroyChildren']();

        // imagesToLoad = mounting_points.filter(u => u['elem']);
        // imagesToLoad = imagesToLoad.length + 1;


        this.drawImage(this.customProduct);
        this.drawParts(this.customProduct['attachments'], baseProportions);

    };

    drawImage = (imageDetails, positioned = null, z = null, baseProportions = null) => {

        let image = new Image();

        image['onload'] = () => {
            let imageWidth = imageDetails['imageDetails']['width'];
            let imageHeight = imageDetails['imageDetails']['height'];
            let imageScale = imageDetails['imageDetails']['scale'];
            let imageOffset = imageDetails['partOffset'];
            let imageCoordinateX;
            let imageCoordinateY;

            if (imageScale) {
                imageWidth *= imageScale;
                imageHeight *= imageScale;

            }

            if (baseProportions) {
                imageCoordinateX = baseProportions[0] * (positioned['left'] / 100);
                imageCoordinateY = baseProportions[1] * (positioned['top'] / 100);

                if (imageOffset) {
                    imageCoordinateX += imageWidth * (imageOffset['x'] / 100);
                    imageCoordinateY += imageHeight * (imageOffset['y'] / 100);

                }
            }


            let canvasImage = new Konva['Image']({
                'x': imageCoordinateX,
                'y': imageCoordinateY,
                'image': image,
                'width': imageWidth,
                'height': imageHeight,
                'productCategory': imageDetails.category.name
            });


            this._layer['add'](canvasImage);
            this._stage['add'](this._layer);
            this._layer['draw']();


            canvasImage.on('click', (e) => {
                const productCategoryName = e.target.attrs.productCategory;
                this.showListProductByCategory(productCategoryName)
            });
        };

        image['src'] = imageDetails['image'];

    };

    drawParts = (attachments,  baseProportions) => {

        for (let typeAttachment in attachments) {
            let currentAttachments = attachments[typeAttachment];
            let element = currentAttachments['elem'];
            let positioned = currentAttachments['positions'];

            if (!element)
                continue;

            this.drawImage(element, positioned, currentAttachments['offset'], baseProportions);

        }
    };

    onSelectCategory = (e, nameCategory) => {
        e.preventDefault();
        this.showListProductByCategory(nameCategory);

    };

    showListProductByCategory = (categoryName) => {
        const filteredProducts = this.state.products.filter(product => {
            if (product.category.name === categoryName) {
                return product;
            }
        });

        this.setState({
            selectedProductCategory:  categoryName,
            filteredProducts
        });

    };

    render () {
        const { filteredProducts, categories} = this.state;

        return (
            <React.Fragment>

            <div id="canvas-wrapper">
                <div id="konvajs-container" ref={this.canvasStage} ></div>
            </div>

            <ul style={{display: 'flex', listStyle: 'none', flexWrap: 'wrap' }}>
                {
                    categories.map(name => {

                        return (
                            <li
                                key={name}
                                style={{marginRight: '10px'}}

                            >
                                <button  onClick={(e) => this.onSelectCategory(e, name)}>
                                    {name}
                                </button>
                            </li>
                        )
                    })
                }
            </ul>
            <ul style={{display: 'flex', listStyle: 'none'}}>
                {
                    filteredProducts.map(product => {

                            return (
                                <li
                                    key={product.id}
                                    style={{display: 'flex', flexDirection: 'column'}}
                                    onClick={() => this.onSelectProduct(product.category.name, product.id)}
                                >
                                    <img src={product.image} alt={product.name} style={{width: '100px', height: '150px'}}/>
                                    {product.name}
                                </li>
                            )
                    })
                }
            </ul>


            </React.Fragment>
        )
    }

}

export default Gun;