import Konva from "konva";

export const createCanvas = (canvasContainerElement, windowWidth) => {
    let canvas;
    let canvasWidth;
    let canvasHeight;
    let typeScreen;
    let canvasScale = {
        x: 0.215,
        y: 0.215,
    };
    let canvasOffset = {
        x: -750,
        y: -500,
    };

    if (windowWidth < 576) {
        canvasWidth = 320;
        canvasHeight = 150;
        typeScreen = 'extra-small';

    } else if (windowWidth >= 576 && windowWidth <= 768) {
        canvasWidth = 576;
        canvasHeight = 250;
        typeScreen = 'small';

    } else if (windowWidth >= 768 && windowWidth <= 992) {
        canvasWidth = 768;
        canvasHeight = 250;
        typeScreen = 'medium';

    } else if (windowWidth >= 992 && windowWidth <= 1200) {
        canvasWidth = 992;
        canvasHeight = 250;
        typeScreen = 'large';

    } else {
        canvasWidth = 1200;
        canvasHeight = 350;
        typeScreen = 'extra-large';

    }

    canvas = new Konva['Stage']({
        'container': canvasContainerElement,
        'width': canvasWidth,
        'height': canvasHeight,
    });

    canvas['setOffset']({
        'x': 0,
        'y': 0,
    });

    switch (typeScreen) {
        case 'extra-large' : {
            canvasScale = {
                x: 0.215,
                y: 0.215,
            };
            canvasOffset = {
                x: -750,
                y: -500,
            };
            break;
        }
        case 'large' : {
            canvasScale = {
                x: 0.18,
                y: 0.18,
            };
            canvasOffset = {
                x: -700,
                y: -200,
            };
            break;
        }

        case 'medium' : {
            canvasScale = {
                x: 0.14,
                y: 0.14,
            };
            canvasOffset = {
                x: -680,
                y: -450,
            };
            break;
        }

        case 'small' : {
            canvasScale = {
                x: 0.1,
                y: 0.1,
            };
            canvasOffset = {
                x: -680,
                y: -500,
            };
            break;
        }

        case 'extra-small' : {
            canvasScale = {
                x: 0.056,
                y: 0.056,
            };
            canvasOffset = {
                x: -730,
                y: -500,
            };
            break;
        }

        default : return
    }

    canvas['scale']({
        'x': canvasScale.x,
        'y' : canvasScale.y,
    });
    canvas['offset']({
        'x': canvasOffset.x,
        'y': canvasOffset.y,
    });

    return canvas;
};